function addTodo(){
  const token = localStorage.getItem('access_token')
  const title = $("#title-todo").val()
  const description = $("#description-todo").val()
  const status = $("#status-todo").val()
  const date = $("#date-todo").val()


  $.ajax({
    method : "POST",
    url : SERVER + "/todos",
    headers : {
      token : token
    }, 
    data : {
      title, description, date, status
    }
  })
  .done(response => {
    todos()
    getAllTodo()
  })
  .fail( err => {
    console.log(err)
  })
}


function todos() {
    $('#login_page').hide()
    $('#landing_navbar').hide()
    $('#register').hide()
    $('#allTodos').hide()
    $('#nowPlayingMovie').hide()
    $('#formadd').hide()
    $('#buttonid').hide()
    $('#filetodos').show()

}

function getAllTodo(){
  const token = localStorage.getItem('access_token')

    $.ajax({
        method: 'GET',
        url: `${SERVER}/todos`,
        headers: {
            token: token,
        },
    })
    .done(response => {
        $('#filetodos').empty()
        response.forEach(element => {
          
          $("#filetodos").append(`
          <div class = "row justify-content-center"><br><br>
          <div class="card" style ="width : 80rem; margin = 20 20">
            <div class="card-header">
            TODO
            </div>
            <div class="card-body style ="width : 100rem">
            <h5 class="card-title">${element.title}</h5>
                <p class="card-text">${element.description}.</p>
                <a href="#" class="btn btn-primary">Detail</a>
                <a href="#" onclick = "formAdd()" class="btn btn-danger">Delete</a>

            </div>
            </div>`)
        });
    }).fail(err=> {
      console.log(err)
    })
}

